import React from 'react';
import DescriptionBlock from './DescriptionBlock';

export default function VideoBlock(props){
    return(
    <div className="content-element video-block">
        <DescriptionBlock title={props.title} meta={props.meta}>
            {props.children}
        </DescriptionBlock>
        <iframe src={props.URL} title={props.title} className="video-embed" frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
    </div>
    );
}