import React from 'react';
import { useStaticQuery, graphql } from "gatsby"
import Loading from '../components/Loading';
import VideoBlock from '../components/VideoBlock';
import Layout from '../components/Layout';

export default function VideosPage(){
    let data = useStaticQuery(graphql`
    query AllVideosQuery {
      allMarkdownRemark(filter: {fileAbsolutePath: {regex: "/videos/"}}) {
        edges {
          node {
            html
            frontmatter {
              videos {
                title
                URL
                meta {
                  person
                  duration
                  time
                  location
                }
              }
            }
          }
        }
      }
    }

    `);
    data = data.allMarkdownRemark.edges
    return(
        
    <Layout title="Wideo">
        {!data ? <Loading /> : data.map((video,i)=>
                <VideoBlock key={i} 
                title={video.node.frontmatter.videos["title"]} 
                meta={video.node.frontmatter.videos["meta"]} 
                URL={video.node.frontmatter.videos["URL"]}
                >
                <div dangerouslySetInnerHTML={{ __html: video.node.html }}></div> 
                </VideoBlock>
        )}
    </Layout>
    );
}